          }
        }
      }
    }
    .testimonial-area {
      .owl-nav {
        .owl-prev {
          margin-right: 0;
          margin-left: 30px;
        }
      }
    }
    .footer_area {
      .f_widget {
        ul {
          padding-right: 0;
        }
      }
    }
    .single-footer-widget {
      .social_widget {
        text-align: left;
      }
      .social_widget {
        a {
          margin-left: 0;
          margin-right: 10px;
        }
      }
