@import "client/footer";
/*---------------------------------------------------- */
html[dir="rtl"] {
  .up_dash_menu {
    display: flex;
  }
  div.dt-buttons {
    float: left;
  }
  button.dt-button,
  div.dt-button,
  a.dt-button {
    border-left: 0;
    border-right: 1px solid $primary-color;
  }
  table {
    &.dataTable {
      thead {
        .sorting:after {
          right: 4px;
          left: auto;
        }
        .sorting_asc:after {
          left: auto;
          right: 0px;
        }
        .sorting_desc:after {
          left: auto;
          right: 2px;
        }
      }
    }
  }

  .dataTables_wrapper {
    .dataTables_filter {
      input {
        margin-right: 0.5em;
        margin-left: 0;
        &:focus {
          @include placeholder {
            left: -16px;
          }
        }
      }
      > label {
        &:before {
          left: auto;
          right: 0;
        }
      }
    }
    .dataTables_paginate {
      .paginate_button {
        i {
          @include transform(rotate(180deg));
          display: inline-block;
        }
      }
    }
    .dataTables_info {
      float: right;
    }
  }

  .common-calendar .fc-state-default.fc-corner-left,
  .common-calendar .fc-button.fc-state-default {
    &:hover {
      @include gradient(90deg, $primary-color2 0%, $primary-color3 100%);
      box-shadow: 0px 10px 20px rgba(108, 39, 255, 0.3);
    }
  }

  .nice-select {
    &:after {
      right: 10px;
    }
    &.bb {
      &:before {
        left: auto;
        right: 0;
      }
    }
    .option {
      padding-right: 18px;
      padding-left: 29px;
      text-align: right;
    }
  }
  table.dataTable, table.dataTable th, table.dataTable td {
    box-sizing: border-box;
  }
}

